import app from '@/common/app.js'; //公共变量
import request from '@/common/request.js';
import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue';
import segmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import uniIcon from '@/components/uni-icon/uni-icon.vue';
import store from '@/store/index.js';
import util from '@/common/util.js';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import '@/static/css/index.css'
import {
    mapState
} from 'vuex';
export default {
    components: {
        uniSwiperDot,
        uniIcon,
        segmentedControl,
        uniLoadMore
    },
    data() {
        return {
            current: '',
            numCard: '',
            treelist: '',
            cardMsg: [],
            bannerlist: [],
            newslist: [],
            schoolNewslist: [],
            sMNewslist: [],
            noticeNewslist: [],
            mode: 'long',
            cur: 0,
            pageSize: 3,
            pageNo: 1,
            isLogin: false,
            domain: "",
            status: "more",
            contentText: {
                contentdown: '加载更多',
                contentrefresh: '加载中',
                contentnomore: '没有更多'
            },
            items: ['新闻动态', '学校新闻', '校友动态', '通知公告'],
        };
    },
    computed: {
        ...mapState(['hasLogin','configParams'])
    },
    onLoad() {
        this.isLogin = this.hasLogin;
    },

    onReachBottom() {
        this.pageNo = this.pageNo + 1;
        var id = this.treelist[this.cur].id;
        request.http({
            url: '/app/index/getIndexNews',
            header: 'application/x-www-form-urlencoded',
            method: "POST",
            data: {
                pageNo : this.pageNo,
                pageSize: this.pageSize,
                id: id
            },
            success: (res) => {
                if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                    if (res.data.data == undefined || res.data.data.length == 0) {
                        this.status = "noMore";
                        setTimeout(() => {
                            this.showLoadMore = false;
                        }, 500);
                    } else {
                        this.bannerlist = res.data.data;
                        var datas = res.data.data;
                        datas.forEach((d) => {
                            this.newslist.push(d);
                        });
                    }
                    if (res.data.data.length < this.pageSize) {
                        this.status = "noMore";
                    }
                }
            },
            error: (res) => {

            }
        }); 
    },
    onShow() {
        this.domain = this.configParams.domain;
        if(this.bannerlist.length == 0){
            request.http({
                url: '/app/index/getIndexNews',
                header: 'application/x-www-form-urlencoded',
                method: "POST",
                data: {
                    pageNo: this.pageNo,
                    pageSize: this.pageSize
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        if (res.data.data == undefined || res.data.data.length == 0) {
                            this.status = "noMore";
                            setTimeout(() => {
                                this.showLoadMore = false;
                            }, 500);
                        } else {
                            this.bannerlist = res.data.data;
                            var datas = res.data.data;
                            datas.forEach((d) => {
                                this.newslist.push(d);
                            });
                        }
                        if (res.data.data.length < this.pageSize) {
                            this.status = "noMore";
                        }
                    }
                },
                error: (res) => {

                }
            }); 
        }
        this.getTreeList();
    },
    methods: {
        onClickItem: function(index,key) {
            this.pageNo = 1;
            if (this.cur !== key) {
                this.cur = key;
            }
            var id = index.id;
            request.http({
                url: '/app/index/getIndexNews',
                header: 'application/x-www-form-urlencoded',
                method: "POST",
                data: {
                    pageNo : this.pageNo,
                    pageSize: this.pageSize,
                    id: id
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        this.newslist = res.data.data;
                    }
                },
                error: (res) => {

                }
            });
        },
        change: function(e) {
            this.current = e.detail.current;
        },
        getTreeList: function (){
            request.http({
                url: '/app/index/getNewsTrees',
                header: 'application/x-www-form-urlencoded',
                method: "POST",
                data: {
                },
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        this.treelist = res.data.data;
                    }
                },
                error: (res) => {

                }
            });
        },

        getSMNewsList: function(e) {
            this.status = "loading";
            request.ajax({
                url: '/news/vsbSMNews.json',
                method: "GET",
                success: (res) => {
                    if (res.data.data.length == 0) {
                        this.status = "noMore";
                        setTimeout(() => {

                        }, 500);
                    } else {
                        this.sMNewslist = this.sMNewslist.concat(res.data.data);
                        this.last_id = res.data.data[res.data.data.length - 1].id;
                    }
                },
                error: (res) => {

                }
            })
        },
        goDetail: function(e) {
            let url = '/pages/index/news/newsDetail?id=' + encodeURIComponent(e.newsid);
            uni.navigateTo({
                url: url
            });
        },
        goBannerDetail: function(e) {
            let url = '/pages/index/news/newsDetail?id=' + encodeURIComponent(e.newsid);
            uni.navigateTo({
                url: url
            });
        }
    },

};
